import React from 'react'
import { Link } from 'react-router-dom'

//header  toggleMenu prop 
const Logo = ({toggleMenu}) => {
  return (
    <h1 className='header__logo'>
      <Link to='/'>
        {/*  Header toggleMenu  */}
        <em aria-hidden="true" onClick={toggleMenu}></em>
        <span>my<br />youtube</span>
      </Link>
    </h1>
  )
}

export default Logo